body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.activeNav{color:teal}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}.list__item .App-link{color:pink}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.PrimaryNav,.SecondaryNav{left:0;right:0;margin:0;width:100%;height:66px}.PrimaryNav ul,.SecondaryNav ul{margin:0;max-width:1256px;height:100%;display:flex;flex-direction:row;justify-content:left;align-items:center}.PrimaryNav ul li,.SecondaryNav ul li{list-style:none}.PrimaryNav ul li a,.SecondaryNav ul li a{display:inline-block;width:-webkit-max-content;width:-moz-max-content;width:max-content;padding:0 10px}.PrimaryNav ul li::after,.SecondaryNav ul li::after{content:"/";color:#212121;opacity:0.8}.PrimaryNav{position:fixed;top:0;background:#212121;border-bottom:1px solid gray}.PrimaryNav a{color:#fafafa}.PrimaryNav ul li::after{color:#fafafa}.PrimaryNav .activeNav{color:pink}.SecondaryNav{position:fixed;background:#fafafa;top:66px;height:33px}.SecondaryNav a{color:#212121}.SecondaryNav .activeNav{color:red}code{display:inline-block;width:90%;margin:10px auto;background:#dfdfdf;border-radius:4px;padding:10px}code .npm-pack{color:#52b96c}

